	orientationchange:function(forceCheck){
		for(var i=0,iMax=Vijs.videoNativePlayer['scrollingUnits'].length;i<iMax;i++){
			var unit = Vijs.videoNativePlayer['scrollingUnits'][i];
			if(unit['bannerIsPopup']==true||forceCheck==true){
				var frame = unit['frame'];
				if(frame.el.clientWidth<frame.el.clientHeight){
					frame.el.contentWindow.document.body.className = 'fullscreen';
				}else{
					frame.el.contentWindow.document.body.className = 'landscape';
				}
			}
		}
	},
