var Vijs = Vijs || {};

Vijs.endcardPlayer = Vijs.endcardPlayer || {
	'network_type_video':Vijs.Configer.getNetworkStatus(),
	'close_button_delay':0,
	init:function (adUnit,adUnitData,callback) {
		var ad = adUnitData['ads'][0];
		// ad['click_url'] = ad['click_url']+'&mb_subid=sdk_test';//user for test the channel
		var preloadResources = [
			ad['icon_url'],
			ad['image_url'],
			'https://hybird.rayjump.com/offerwall/img/reward/privacy.png',
			'https://hybird.rayjump.com/offerwall/img/reward/star.png',
		];
		var player = this;
		player['close_button_delay'] = adUnitData['setting']['close_button_delay'];
		var endcard = {
			templateUrl : Vijs.Configer['baseSrc']+'/endcardTemplate.js?v=8',//Vijs.Configer['host']+
			loadComplete:false,
			warpper:null,
			preload: function(offer){
				var startLoadTime = (new Date()).getTime();
				Vijs.Util.preloadResource(endcard.templateUrl,function(){
					Vijs.Util.domHelper('script').attr({
						'type':'text/javascript',
						'src':endcard.templateUrl
					}).insertTo(document.body);
					Vijs.Util.preloadResourceArray(preloadResources,function(){
						if(callback) callback();
						var endLoadTime = (new Date()).getTime();
						var loadDuration = endLoadTime - adUnit['videoPlayer']['startLoadTime'];

						Vijs.Logger.post({
							'eventType':'videoLoadSuccess',
							'offer_id':ad['id'],
							'duration':adUnit['videoPlayer']['loadDuration'],
							'full_duration':loadDuration,
							'video_size':ad['video_size'],
						});
					},loadFaile);
				},loadFaile);

				function loadFaile(reason,imageUrl){
					Vijs.Logger.post({
						'eventType':'videoLoadFail',
						'offer_id':ad['id'],
						'image_url':imageUrl,
						'reason':reason? reason.type.toString() :'load error',
						'notice_url':ad['notice_url'],
						'unit_id':adUnit['unitid']
					});

					Vijs.Logger.post({
						'eventType':'endcardShow',
						'network_type_video':adUnit['videoPlayer']['network_type_video'],
						'network_type_endcard':Vijs.Configer.getNetworkStatus(),
						'offer_id':ad['id'],
						'image_url':ad['image_url'],
						'success':0,
						'reason':2,
						'notice_url':ad['notice_url'],
						'unit_id':adUnit['unitid']
					});
				};
				
			},
			render:function(renderCallback){
				var frameWarpper = Vijs.Util.domHelper('div');
		        var frame = Vijs.Util.domHelper('iframe');
		        this['warpper'] = frameWarpper;

		        frameWarpper.css({
		          'position': 'fixed',
		          'z-index': 2147483646,
		          'top': '0px',
		          'left': '0px',
		          'right': '0px',
		          'bottom': '0px'
		        });
		        var frameWidth = Vijs.Configer['currentScreen']['width'];
		        var frameHeight = Vijs.Configer['currentScreen']['height'];
		        frame.css({
		          'width': '100%',
		          'height': '100%',
		          'margin': '0',
		          'padding': '0',
		          'border': 'none'
		        }).insertTo(frameWarpper);
		        frameWarpper.insertTo(document.body);
		        frame.el.contentWindow.document.documentElement.innerHTML = decodeURIComponent(Vijs.endcardPlayer['template']['endcard.v3']['html']) ;



		        // var v3Script = Vijs.Util.domHelper('script').attr('src','src/endscreen.v3.js?v=2').attr('type','text/javascript');
		        // v3Script.insertTo(frame.el.contentWindow.document.body);


		        
		        // console.log(frame.el.contentWindow.utils)

		        setTimeout(function(){
		        	frame.el.contentWindow.eval(decodeURIComponent(Vijs.endcardPlayer['template']['endcard.v3']['js']));
		        	frame.el.contentWindow.render(adUnitData);
		        	if(renderCallback) renderCallback();
		        },100);
		        
		        
		        
			},
			play:function(playCallback,param){
				this.render(playCallback);
				this.showClose();
				Vijs.Logger.post({
					'eventType':'endcardShow',
					'network_type_video':adUnit['videoPlayer']['network_type_video'],
					'network_type_endcard':Vijs.Configer.getNetworkStatus(),
					'offer_id':ad['id'],
					'image_url':ad['image_url'],
					'success':1,
					'notice_url':ad['notice_url'],
					'unit_id':adUnit['unitid']
				});
			},
			pause:function(){
				
			},
			showClose:function(){
				var me = this;
				var frameWarpper = me['warpper'];
				var frameWarpperSize = frameWarpper.size();
				var closeWidth = Math.floor(frameWarpperSize['width'] / 15);
				// Vijs.Render.addCloseButton(frameWarpper, closeWidth,'righttop',this.close);
				Vijs.Render.addCountingCloseButton(frameWarpper,player['close_button_delay'], closeWidth,'righttop',function(){ 
					me.close(); 
				});
			},
			close:function(){
				if(adUnit.reload) adUnit.reload();
				// Vijs.Logger.post({
				// 	'eventType':'videoClose',
				// 	'offer_id':ad['id'],
				// 	'notice_url':ad['notice_url'],
				// 	'unit_id':adUnit['unitid'],
				// 	'view_length':video.currentTime,
				// 	'video_size':ad['video_size'],
				// 	'reason':1,//1.skip/2.break
				// });
			},
		}

		endcard.preload(ad);

		return endcard;
	}
};

