var Vijs = Vijs || {};

Vijs.init = Vijs.init || function(mvrvtag){
	Vijs.Configer.preInit(mvrvtag);
	Vijs.Util.addListener(document,'DOMContentLoaded',function(){
		var ua = window.navigator.userAgent.toLowerCase();
		if(ua.indexOf('googlebot')>=0||ua.indexOf('search console')>=0) return;
		Vijs.Logger.init();
		Vijs.Configer.init(mvrvtag,function(unitid){
			var me = this;
			var mvrvtagEl = Vijs.Util.domHelper(mvrvtag);
			var win = Vijs.Util.domHelper(window);
			if(unitid){
				// Vijs.Offer.init(mvrvtag);

				var loadedEvent = mvrvtagEl.attr('listen-event-loaded');
				var rewardEvent = mvrvtagEl.attr('listen-event-reward');
				var rewardSettingEvent = mvrvtagEl.attr('listen-event-getrewardsetting');
				var rewardSettingEvent = mvrvtagEl.attr('listen-event-getrewardsetting');

				var showEvent = mvrvtagEl.attr('trigger-event-show');
				var closeEvent = mvrvtagEl.attr('trigger-event-close');
				var pauseEvent = mvrvtagEl.attr('trigger-event-pause');

				var adConfig = {
					'unitid':unitid,
					'loadedCallback':function(){
						win.trigger(loadedEvent);
						if(rewardSettingEvent){
							win.trigger(rewardSettingEvent,me.innerAd.getRewardSetting());
						}
					},
					'rewardedCallback':function(reward_name,reward_amount,unitid){
						win.trigger(rewardEvent,[reward_name,reward_amount,unitid]);
					}
				};

				me.innerAd = Vijs.setAD(adConfig);
				if(showEvent) win.bind(showEvent,function(){
					me.innerAd.show();
				});
				if(closeEvent) win.bind(closeEvent,function(){
					me.innerAd.close();
				});
				if(pauseEvent) win.bind(pauseEvent,function(){
					me.innerAd.pause();
				});

				if(pauseEvent) win.bind(pauseEvent,function(){
					me.innerAd.pause();
				});

			} 

			

			// var myAd = Vijs.setAD({
			// 	'unitid':31327,
			// 	loadedCallback:function(){
			// 		alert('success')
			// 	},
			// 	rewardedCallback:function(reward_name,reward_amount,unitid){
			// 		alert(reward_name);
			// 		console.log(reward_amount);
			// 	}
			// });



		});

		var gct = Vijs.Cache.getLocalStorage('gct');//get cookie time
		var isSendGCT = gct ? ((new Date()).getTime()-parseInt(gct)>86400000?true:false):true;
		if(isSendGCT){
			Vijs.Logger.post({'eventType':'cookieInfo'});
			Vijs.Cache.setLocalStorage('gct',(new Date()).getTime());
		}
	});

	
}
Vijs.setAD = Vijs.setAD || function(adConfig){

	var adUnit = Vijs.Offer.addAdUnit(adConfig);
	var adObj = {
		'unitid':adConfig['unitid'],
		show:function(){
			if(!adUnit) return false;
			if(!Vijs.Offer.updateDailyPlayCap(adUnit)||!adUnit.show) return false;
			// if(adUnit['videoPlayer']) adUnit['videoPlayer'].play();
			// return adUnit['loadComplete'];
			return adUnit.show();
			
		},
		close:function(){
			if(!adUnit||!adUnit.close) return false;
			adUnit.close();
		},
		pause:function(){
			if(!adUnit||!adUnit.pause) return false;
			adUnit.pause();
		},
		checkIsLoadComplete:function(){
			if(!adUnit) return false;
			return adUnit['loadComplete'];
		},
		getRewardSetting:function(){
			if(!adUnit) return false;
			return adUnit['mainSetting']['reward']? adUnit['mainSetting']['reward'][0]:null;
		}
	}
	return adObj;
};


